<template>
  <div v-if="selectComp && authComp">
    <vxe-modal v-model="visible" title="发票邮寄单号" width="460" height="200" esc-closable>
      <div>{{ selectComp.invoice }}</div>
      <template v-if="authComp.expressName">
        <div>{{ authComp.expressName }} {{ authComp.expressNo }}</div>
      </template>
      <template v-else>
        <div>发票申请中...</div>
      </template>
    </vxe-modal>
  </div>
</template>

<script>
import { mapState } from 'vuex'

export default {
  name: 'InvoiceInfo',
  props: {
    code: String
  },
  data () {
    return {
      visible: false
    }
  },
  computed: {
    ...mapState([
      'loginToken',
      'loginInfo',
      'appList'
    ]),
    authComp () {
      if (this.loginToken && this.loginInfo && this.loginInfo.compList) {
        return this.loginInfo.compList.find(comp => comp.appCode === this.code)
      }
      return null
    },
    selectComp () {
      return this.appList.find(item => item.code === this.code)
    }
  },
  methods: {
    open () {
      this.visible = true
    },
    submit () {}
  }
}
</script>
