<template>
  <div v-if="supportQQComp" class="support-page">
    <h2 class="support-title">
      <span>{{ supportQQComp.name }}</span>
      <span class="price-unit">￥{{ supportQQComp.amount }}</span>
      <span style="font-size: 12px">/年</span>
      <span style="font-size: 12px;font-weight: 400;">（{{ supportQQComp.features.join('; ') }}）</span>
    </h2>
    <div class="support-group">
      <div class="qq-img">
        <template v-if="disabledSupport">
          <div title="抱歉！当前不在支持的时间之内！请在工作日 9:30 ~ 17:30 时间内申请！">
            <img src="" style="width: 189px; height: 183px">
          </div>
        </template>
        <template v-else>
          <img v-if="useIssue" src="../assets/image/support/qq.png" style="width: 189px; height: 183px">
          <img v-else src="" style="width: 189px; height: 183px">
        </template>
      </div>
      <div class="qq-step">
        <ul class="step-wrapper">
          <li style="font-size: 15px;color: red;font-weight: 700;user-select: none;" title="如果不会使用在线链接来描述问题，建议通过 issues 反馈问题！">
            <div>
              <label>
                <input v-model="useIssue" type="radio" name="useIssue" value="">（我不会使用 “<a class="link" href="/vxe-table/issues" target="_blank">在线 Demo 演示问题</a>”）
              </label>
            </div>
            <div>
              <label>
                <input v-model="useIssue" type="radio" name="useIssue" value="1">（我会使用 “<a class="link" href="/vxe-table/issues" target="_blank">在线 Demo 演示问题</a>”）
              </label>
            </div>
          </li>
          <template v-if="!useIssue">
            <li>建议您通过 <a class="link" href="https://github.com/x-extends/vxe-table/issues" target="_blank">issues</a> 反馈问题即可</li>
          </template>
          <template v-if="disabledSupport">
            <li v-if="useIssue" title="抱歉！当前不在支持的时间之内！请在工作日 9:30 ~ 17:30 时间内申请！">抱歉，当前不在支持的时间之内，无法加入</li>
          </template>
          <template v-else>
            <template v-if="useIssue">
              <li>
                <div>左侧扫码申请加入群或点击 <a target="_blank" href="https://qm.qq.com/cgi-bin/qm/qr?k=K0y8dWpsIwFtcpKfb4LlVx44bujw1FaL&jump_from=webapi"><img border="0" src="//pub.idqqimg.com/wpa/images/group.png" alt="vxe-table 技术支持群" title="vxe-table 技术支持群"></a></div>
                <vxe-button class="btn" status="danger" size="mini" @click="$refs.compOrder.open()" style="margin-left: 15px;">扫码付款</vxe-button>
              </li>
            </template>
          </template>
        </ul>
      </div>
    </div>
    <comp-order ref="compOrder" :code="code"></comp-order>
  </div>
</template>

<script>
import { mapState } from 'vuex'

export default {
  data () {
    return {
      code: 'SupportQQ',
      useIssue: ''
    }
  },
  computed: {
    ...mapState([
      'appList',
      'serveTime'
    ]),
    supportQQComp () {
      return this.appList.find(item => item.code === 'SupportQQ')
    },
    disabledSupport () {
      const currDate = new Date(this.serveTime)
      const day = currDate.getDay()
      const hours = currDate.getHours()
      if (day === 0 || !((hours >= 10 && hours <= 11) || (hours >= 14 && hours <= 17) || (hours >= 20 && hours <= 21))) {
        return true
      }
      return false
    }
  }
}
</script>

<style lang="scss" scoped>
.support-title {
  text-align: center;
}
.price-unit {
  font-size: 18px;
  color: #ff6700;
  padding: 0 5px;
  font-weight: 700;
}
.support-group {
  margin: 15px auto;
  width: 520px;
  display: flex;
  flex-direction: row;
  .qq-img {
    flex-shrink: 0;
  }
  .qq-step {
    flex-grow: 1;
  }
  .step-wrapper {
    & > li {
      list-style-type: circle;
      line-height: 32px;
    }
  }
  .pay-link {
    color: #22AB38;
    cursor: pointer;
  }
}
.is-disabled {
  cursor: no-drop;
}
.support-pay {
  margin-top: 50px;
  text-align: center;
}
</style>
